import Quill from 'quill';
let Parchment = Quill.import('parchment');
let Container = Quill.import('blots/container');
let Inline = Quill.import('blots/inline');
let Block = Quill.import('blots/block');

/**
 * [MkTd description]
 */
class MkTd extends Block {
    static create() {
        let node = super.create();
        node.setAttribute('contentEditable', true);
        return node;
    }
    constructor(domNode) {
        super(domNode);
        domNode.addEventListener('keydown', e => {
            console.log(e);
            switch (e.keyCode) {
                case 13:
                    e.preventDefault();
                    let newTr = Parchment.create('mkTr');
                    this.parent.parent.appendChild(newTr);
                    window.setTimeout(() => newTr.children.head.domNode.focus(), 0);
                    break;
                case 9:
                    e.preventDefault();
                    let newTd = Parchment.create('mkTd');
                    this.parent.appendChild(newTd);
                    window.setTimeout(() => newTd.domNode.focus(), 0);
                    break;
                case 8:
                    break;
                default:
                    break;
            }
        });
    }
}

MkTd.blotName = 'mkTd';
MkTd.tagName = 'td';
Quill.register('formats/mkTd', MkTd);

/**
 * [MkTr description]
 */
class MkTr extends Container {
    static create() {
        let node = super.create();
        return node;
    }
}

MkTr.scope = Parchment.Scope.BLOCK_BLOT;
MkTr.blotName = 'mkTr';
MkTr.tagName = 'tr';
MkTr.defaultChild = 'mkTd';
MkTr.allowedChildren = [MkTd];
Quill.register('formats/mkTr', MkTr);

/**
 * [MkTable description]
 */
class MkTable extends Container {
    static create() {
        let node = super.create();
        node.setAttribute('contentEditable', false);
        return node;
    }
};

MkTable.scope = Parchment.Scope.BLOCK_BLOT;
MkTable.blotName = 'mkTable';
MkTable.tagName = 'table';
MkTable.defaultChild = 'mkTr';
MkTable.allowedChildren = [MkTr];
Quill.register('formats/mkTable', MkTable);
