import Quill from 'quill';
// let List = Quill.import('formats/list');
let Parchment = Quill.import('parchment');
let Container = Quill.import('blots/container');
let Block = Quill.import('blots/block');

class Checkbox extends Block {

}

Checkbox.blotName = 'checkbox';
Checkbox.tagName = 'input';

class CheckboxItem extends Container {

}

CheckboxItem.blotName = 'checkboxItem';
CheckboxItem.tagName = 'li';
Checkbox.defaultChild = 'checkbox';
Checkbox.allowedChildren = [Checkbox];

class CheckboxGroup extends Container {
    static create(value) {
        let tagName = 'UL';
        let node = super.create(tagName);
        if (value === 'checked' || value === 'unchecked') {
            node.setAttribute('data-checked', value === 'checked');
        }
        return node;
    }

    static formats(domNode) {
        if (domNode.tagName === 'UL') {
            if (domNode.hasAttribute('data-checked')) {
                return domNode.getAttribute('data-checked') === 'true' ? 'checked' : 'unchecked';
            }
            else {
                return 'bullet';
            }
        }
        return undefined;
    }

    constructor(domNode) {
        super(domNode);
        const listEventHandler = e => {
            if (e.target.parentNode !== domNode) {
                return;
            }
            let format = this.statics.formats(domNode);
            let blot = Parchment.find(e.target);
            if (format === 'checked') {
                blot.format('checkboxGroup', 'unchecked');
            }
            else if(format === 'unchecked') {
                blot.format('checkboxGroup', 'checked');
            }
        };

        domNode.addEventListener('touchstart', listEventHandler);
        domNode.addEventListener('mousedown', listEventHandler);
    }

    format(name, value) {
        if (this.children.length > 0) {
            this.children.tail.format(name, value);
        }
    }

    formats() {
        // We don't inherit from FormatBlot
        return {[this.statics.blotName]: this.statics.formats(this.domNode)};
    }

    insertBefore(blot, ref) {
        if (blot instanceof CheckboxItem) {
            super.insertBefore(blot, ref);
        }
        else {
            let index = ref == null ? this.length() : ref.offset(this);
            let after = this.split(index);
            after.parent.insertBefore(blot, after);
        }
    }

    optimize(context) {
        super.optimize(context);
        let next = this.next;
        if (next != null && next.prev === this
                && next.statics.blotName === this.statics.blotName
                && next.domNode.tagName === this.domNode.tagName
                && next.domNode.getAttribute('data-checked') === this.domNode.getAttribute('data-checked')) {
            next.moveChildren(this);
            next.remove();
        }
    }

    replace(target) {
        if (target.statics.blotName !== this.statics.blotName) {
            let item = Parchment.create(this.statics.defaultChild);
            target.moveChildren(item);
            this.appendChild(item);
        }
        super.replace(target);
    }
}
CheckboxGroup.blotName = 'checkboxGroup';
CheckboxGroup.scope = Parchment.Scope.BLOCK_BLOT;
CheckboxGroup.tagName = 'ul';
CheckboxGroup.defaultChild = 'checkboxItem';
CheckboxGroup.allowedChildren = [CheckboxItem];

// Quill.register(List);
Quill.register(Checkbox);
Quill.register(CheckboxItem);
Quill.register(CheckboxGroup);
